body{margin:0;padding:0;font-size:20px;font-family:"Quicksand",sans-serif;color:#fbfbfb;background-color:#f5f5f5;scroll-behavior:smooth;box-sizing:border-box}a{text-decoration:none;color:#fbfbfb}input{outline:none}.input-container{position:relative;top:15px}.input-container input{width:430px;height:30px;padding:7px;font-size:1.1rem;border-radius:8px;transition:all .4s ease;border:none;box-shadow:0px 0px 13px -4px rgba(0,0,0,.75);z-index:21;background-color:#fff}@media screen and (max-width: 610px){.input-container input{width:90%}}.input-container input:focus{border-bottom-left-radius:0;border-bottom-right-radius:0}.input-container .input-dropdown{display:none;position:absolute;background-color:#fff;width:100%;z-index:15;max-height:200px;overflow-y:scroll;border-bottom-left-radius:8px;border-bottom-right-radius:8px}.input-container .input-dropdown .input-element{padding:5px 0px;cursor:pointer;transition:all .3s ease;color:#1e272e}.input-container .input-dropdown .input-element span{transition:all .3s ease;margin-left:5px}.input-container .input-dropdown .input-element:hover span{margin-left:10px}.input-container.opened .input-dropdown{display:block}
